.box {
	max-width: $containerWidth;
	width: 100%;
	margin: 0 auto;
}

.row {
	display: flex;
	flex-wrap: wrap;
	margin-left: -10px;
	margin-right: -10px;
}

.row [class*='col-'] {
	padding-left: 10px;
	padding-right: 10px;
}

.row.no_gutter {
	margin-left: 0;
	margin-right: 0;
}

.row.no_gutter [class*='col-'] {
	padding-left: 0;
	padding-right: 0;
}

.col-6 {
	width: 50%;
	@include tablet-sm-768 {
		width: 100%;
	}
}

.col-4 {
	width: 33.3333333%;
	@include tablet-md-1024 {
		width: 50%;
	}
	@include tablet-sm-768 {
		width: 100%;
	}
}

.col-3 {
	width: 25%;
	@include tablet-md-1024 {
		width: 33.33333%;
	}
	@include tablet-sm-768 {
		width: 50%;
	}
	@include mobile-sm-480 {
		width: 100%;
	}
}

.wrapper_top {
	padding-bottom: 130px;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: top center;

	header {
		padding: 31px 0 57px 0;

		.logo {
			position: relative;
			top: -15px;
		}

		.box {
			display: flex;
			align-items: center;
		}

		.wrap_social {
			margin-left: auto;
			display: flex;
			flex-direction: column;
			align-items: flex-end;

			.top_line {
				display: flex;
				align-items: center;
				margin-bottom: 17px;
			}

			.tel {
				font-weight: bold;
				font-size: 26px;
				color: #FFFFFF;
				margin-left: 15px;
				transition: .1s;

				&:hover {
					opacity: .7;
				}
			}

			.wrap_btn {
				display: flex;
				align-items: center;
				margin-bottom: 21px;

				.btn_2 {
					margin-right: 10px;

					&:last-child {
						margin-right: 0;
					}
				}
			}

			.go_website {
				display: flex;
				align-items: center;
				justify-content: flex-end;
				font-size: 14px;
				line-height: 16px;
				text-align: right;
				color: rgba(255, 255, 255, .6);
				transition: .1s;

				img {
					margin-left: 16px;
				}

				&:hover {
					opacity: .7;
				}
			}
		}
	}
}

.main_menu {
	position: relative;
	top: -15px;
	margin-left: 160px;

	ul {
		display: flex;
		align-items: center;

		li {
			margin-right: 74px;
			list-style: none;

			&:last-child {
				margin-right: 0;
			}

			a {
				font-size: 14px;
				line-height: 16px;
				text-align: center;
				color: #FFFFFF;
				transition: .1s;

				&:hover {
					opacity: .7;
				}
			}
		}
	}
}

.social_icon {
	display: flex;
	align-items: center;

	li {
		list-style: none;
		margin-right: 5px;

		&:last-child {
			margin-right: 0;
		}

		a {
			transition: .1s;
			width: 100%;
			height: 100%;
			display: block;

			&:hover {
				opacity: .7;
			}
		}
	}
}


.btn_2 {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	height: 38px;
	padding: 0 21px;
	border: 1px solid #FFFFFF;
	border-radius: 5px;
	font-size: 14px;
	line-height: 16px;
	color: #FFFFFF;
	transition: .1s;

	&:hover {
		background: #fff;
		color: #333333;
	}
}

.btn {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	height: 55px;
	padding: 0 39px;
	background: #6CE5E8;
	border-radius: 5px;
	font-size: 16px;
	line-height: 19px;
	color: #2B2B2B;
	border: 1px solid transparent;
	transition: .1s;

	&:hover {
		background: #fff;
		border: 1px solid #2B2B2B;
	}
}

.section_top {
	.left_box {
		max-width: 725px;
	}

	.wrap_partner {
		display: flex;
		align-items: center;
		margin-bottom: 38px;

		.icon {
			width: 85px;
			height: 26px;
			background: #FFFFFF;
			border-radius: 5px;
			display: flex;
			align-items: center;
			justify-content: center;
		}

		p {
			font-size: 16px;
			line-height: 19px;
			color: #FFFFFF;
			margin-left: 19px;
		}
	}

	.title_h3 {
		font-weight: 300;
		font-size: 28.1114px;
		line-height: 33px;
		text-transform: uppercase;
		color: #FFFFFF;
	}

	.title_h1 {
		font-style: italic;
		font-weight: bold;
		font-size: 141.596px;
		line-height: 166px;
		text-transform: uppercase;
		color: #FFFFFF;
		text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
		margin-bottom: 8px;
		margin-left: -9px;
	}

	.desc {
		font-size: 20px;
		line-height: 130%;
		color: #FFFFFF;
		margin-bottom: 38px;
		font-weight: 300;
	}

	.wrap_list {
		display: flex;
		margin-bottom: 64px;

		.item {
			display: flex;
			align-items: center;
			margin-right: 40px;

			&:last-child {
				margin-right: 0;
			}

			.icon {
				margin-right: 26px;
				flex-shrink: 0;
			}

			p {
				font-size: 18px;
				line-height: 130%;
				color: #FFFFFF;
			}
		}
	}
}

.wrap_consultation {
	display: flex;
	align-items: center;

	.consultation_link {
		margin-left: 31px;
		display: flex;
		align-items: center;

		.text {
			font-size: 16px;
			line-height: 19px;
			color: #FFFFFF;
			border-bottom: 1px dashed #FFFFFF;
			transition: .1s;

			&:hover {
				opacity: .7;
			}
		}

		.icon {
			flex-shrink: 0;
			margin-right: 9px;
		}
	}
}

.title_h2 {
	font-weight: 300;
	font-size: 46px;
	line-height: 54px;
	text-align: center;
	text-transform: uppercase;
	color: #333333;
	margin-bottom: 90px;
}

.news {
	padding: 106px 0 56px 0;
	background: #fff;
}

.slider_news_css {
	overflow: hidden;
}

.slider_wrap_btn {
	display: flex;
	align-items: center;
	width: 250px;
	margin-left: auto;

	> div {
		position: static;
		transform: none;
		width: auto;
		height: auto;
		font-size: 16px;
		line-height: 19px;
		color: #2E686A;
		margin: 0;

		.icon {
			width: 25px;
		}

		&:before, &:after {
			content: none;
		}
	}

	.swiper-button-prev {
		transition: .1s;

		&:hover {
			opacity: .7;
		}

		.icon {
			margin-right: 8px;

			img {
				display: flex;
			}
		}
	}

	.swiper-button-next {
		transition: .1s;

		&:hover {
			opacity: .7;
		}

		.icon {
			margin-left: 8px;

			img {
				display: flex;
			}
		}
	}
}

.module__post {
	margin-bottom: 26px;

	.image {
		display: block;
		height: 215px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 10px 10px 0px 0px;
		}
	}

	.content_box {
		padding: 32px;
		background: #F1FBFB;
		border-radius: 0 0 10px 10px;

		.title_h5 {
			margin-bottom: 12px;

			a {
				font-weight: bold;
				font-size: 18px;
				line-height: 130%;
				color: #333333;
				transition: .1s;

				&:hover {
					opacity: .7;
				}
			}
		}

		.text {
			font-size: 14px;
			line-height: 150%;
			color: #333333;
			margin-bottom: 19px;
		}

		.wrap_bottom {
			display: flex;
			justify-content: space-between;

			.date {
				font-size: 14px;
				color: #8AABAC;
			}
		}
	}
}

.more {
	display: flex;
	align-items: center;
	font-size: 16px;
	line-height: 19px;
	color: #2E686A;
	transition: .1s;

	.icon {
		margin-left: 12px;
		flex-shrink: 0;
		position: relative;
		right: 0;
		transition: .1s;

	}

	&:hover {
		opacity: .7;

		.icon {
			right: -10px;
		}
	}
}

.swiper-pagination-bullet {
	width: 9px;
	height: 9px;
	background: #CDEFEF;
	opacity: 1;
}

.swiper-pagination-bullet-active {
	background: #2E686A;
}

.organizations {
	padding: 110px 0 105px 0;
	background: linear-gradient(180deg, #BCF2F0 0%, rgba(235, 255, 254, 0) 100%), #EBFFFE;

	.wrap_box {
		display: flex;
	}
}

.module_box_overlay {
	position: relative;
	border-radius: 10px;
	padding: 55px;
	margin-right: 20px;
	max-width: 590px;
	background-repeat: no-repeat;
	background-size: 590px;

	.inner_wrap {
		position: relative;
	}

	&:last-child {
		margin-right: 0;
	}

	&:before {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(0deg, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8));
		border-radius: 10px;
	}

	.title_h4 {
		font-weight: 300;
		font-size: 36px;
		line-height: 42px;
		text-transform: uppercase;
		color: #FFFFFF;
		margin-bottom: 15px;
	}

	.sub_title {
		font-size: 18px;
		line-height: 21px;
		color: #FFFFFF;
		margin-bottom: 55px;
	}

	.list {
		margin-bottom: 52px;

		li {
			list-style: none;
			display: flex;
			align-items: center;
			margin-bottom: 31px;
			font-size: 18px;
			line-height: 130%;
			color: #FFFFFF;

			.icon {
				flex-shrink: 0;
				margin-right: 30px;
			}

			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}

.module__projects {
	position: relative;
	height: 285px;
	background-repeat: no-repeat;
	border-radius: 10px;
	margin-bottom: 26px;

	&:before {
		content: '';
		position: absolute;
		width: 100%;
		height: 100%;
		background: linear-gradient(0deg, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7));
		left: 0;
		top: 0;
		border-radius: 10px;
	}

	.inner_wrap {
		display: flex;
		justify-content: space-between;
		flex-direction: column;
		width: 100%;
		height: 100%;
		position: relative;
		text-align: center;
		padding-top: 90px;

		.sub_title {
			font-size: 14px;
			line-height: 16px;
			text-align: center;
			color: #FFFFFF;
			margin-bottom: 11px;
		}

		.title {
			font-size: 22px;
			line-height: 26px;
			text-transform: uppercase;
			color: #FFFFFF;
		}

		.more {
			color: #6CE5E8;
			display: flex;
			justify-content: center;
			margin-bottom: 26px;
		}
	}
}

.projects {
	padding-bottom: 106px;
	counter-reset: my-sec-counter 0;

	.quote_box {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-top: 43px;

		.consultation_link {
			.text {
				color: #364D4D;
				border-color: #364D4D;
			}
		}

		.module__blockquote {
			display: flex;

			.left_box {
				text-align: right;
				margin-right: 26px;
				max-width: 455px;

				blockquote {
					font-style: italic;
					font-weight: 300;
					font-size: 18px;
					line-height: 21px;
					letter-spacing: 0.065em;
					color: #000000;
				}

				.name {
					margin-top: 4px;
					font-weight: 300;
					font-size: 14px;
					line-height: 16px;
					letter-spacing: 0.065em;
					color: #717171;
				}
			}

			.avatar {
				img {
					border-radius: 50%;
					box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
					width: 70px;
					height: 70px;
					object-fit: cover;
				}
			}
		}
	}
}

.module__line {
	position: relative;
	background-repeat: no-repeat;
	border-radius: 10px;
	margin-bottom: 32px;
	padding: 28px 28px 28px 99px;
	counter-increment: my-sec-counter;
	height: 197px;
	display: flex;
	align-items: center;

	.inner_wrap {
		position: relative;

		//&:before {
		//	content: '';
		//	position: absolute;
		//	left: 20px;
		//	bottom: -45px;
		//	width: 8px;
		//	height: 41px;
		//	background: url("../img/arrow_down.svg") no-repeat;
		//}
	}

	&:last-child {
		.inner_wrap {
			&:before {
				content: none;
			}
		}
	}

	&:after {
		content: counter(my-sec-counter);
		position: absolute;
		left: -25px;
		top: -15px;
		font-style: italic;
		font-weight: bold;
		font-size: 230px;
		line-height: 100%;
		color: #EBFFFE;
	}

	&:before {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(0deg, rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.75));
		border-radius: 10px;
	}

	.title {
		font-weight: bold;
		font-size: 20px;
		line-height: 150%;
		color: #FFFFFF;
		margin-bottom: 7px;
		display: flex;
		align-items: center;

		.icon {
			margin-right: 12px;
			flex-shrink: 0;

			img {
				display: flex;
				align-items: center;
			}
		}
	}

	.text {
		font-size: 17px;
		line-height: 150%;
		color: #FFFFFF;
	}
}

.unique_team {
	padding-bottom: 112px;

	.join_team {
		display: flex;
		align-items: center;
		justify-content: center;
		margin-top: 22px;

		.title {
			margin-right: 85px;
			font-weight: 300;
			font-size: 24px;
			line-height: 130%;
			color: #000000;
		}

		.consultation_link {
			.text {
				color: #364D4D;
				border-color: #364D4D;
			}
		}
	}
}

