.section_map {
	background: #fff;
}

.overlay {
	position: fixed;
	width: 100%;
	height: 100vh;
	top: 0;
	left: 0;
	opacity: 0;
	pointer-events: none;
	background: rgba(19, 100, 112, .8);
	z-index: 99;
	overflow-y: auto;
	display: flex;
	justify-content: center;
	padding: 4rem 0;
}

.popup {
	max-width: 417px;
	width: calc(100% - 30px);
	background: #fff;
	position: relative;
	overflow: hidden;
	padding: 50px 60px;
	margin: auto 0;
	border-radius: 10px;
	@media (max-width: 768px) {
		padding: 50px 15px 15px 15px;
	}

	p {
		max-width: none;
		margin-bottom: 1rem;

		&:last-child {
			margin-bottom: 0;
		}
	}

	.title_h3 {
		font-weight: 300;
		font-size: 30px;
		line-height: 35px;
		text-transform: uppercase;
		color: #000000;
		margin-bottom: 18px;
	}

	.sub_title {
		font-size: 18px;
		line-height: 21px;
		color: #000000;
		margin-bottom: 37px;
	}

	.input_def {
		margin-bottom: 9px;
	}

	.btn {
		width: 100%;
		margin-bottom: 13px;
	}

	.info_text {
		font-size: 14px;
		line-height: 16px;
		text-align: center;
		color: #545454;
		max-width: 224px;
		margin: 0 auto;
	}

	&.popup_2 {
		max-width: 1280px;

		.wrap_box {
			display: flex;

			.left_box {
				width: 451px;
				margin-right: 67px;
				flex-shrink: 0;
			}

			.right_box {

				.title_h3 {
					font-size: 36px;
					line-height: 42px;
					color: #333333;
					margin-bottom: 25px;
				}

				p {
					font-weight: 300;
					font-size: 16px;
					line-height: 130%;
					text-align: justify;
					color: #000000;
					margin-bottom: 30px;
				}

				.implementation_period, .status {
					font-weight: 300;
					font-size: 16px;
					line-height: 130%;
					color: #000000;

					strong {
						color: #000000;
						font-weight: 400;
					}
				}

				.btn {
					margin-top: 30px;
					width: auto;
				}
			}
		}
	}

	&.popup_3 {
		.wrap_box {
			.right_box {
				.title_h3 {
					font-size: 18px;
					line-height: 130%;
					margin-bottom: 21px;
				}
			}
		}
	}
}

.input_def {
	width: 100%;
	height: 67px;
	background: #E8F8F8;
	border-radius: 5px;
	padding-left: 34px;
	padding-right: 34px;
	display: flex;
	align-items: center;
	font-size: 18px;
	color: #A4A4A4;
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
	color: #A4A4A4;
}

::-moz-placeholder { /* Firefox 19+ */
	color: #A4A4A4;
}

:-ms-input-placeholder { /* IE 10+ */
	color: #A4A4A4;
}

:-moz-placeholder { /* Firefox 18- */
	color: #A4A4A4;
}

.body_overflow {
	overflow: hidden;
}

.close_popup {
	position: absolute;
	right: 20px;
	top: 20px;
	cursor: pointer;
}

.gallery_css {
	.swiper-slide {
		height: 451px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			vertical-align: middle;
			border-radius: 10px;
		}
	}
}

.mySwiper, .mySwiper_3 {
	margin-top: 10px;

	.swiper-slide {
		height: 82px;
	}
}
