footer {
	padding: 36px 0;

	.box {
		display: flex;
		align-items: center;
		justify-content: space-between;

		.footer_menu {
			ul {
				display: flex;
				align-items: center;
			}

			li {
				list-style: none;
				margin-right: 15px;

				&:last-child {
					margin-right: 0;
				}

				a {
					font-size: 14px;
					line-height: 16px;
					color: #979797;
					transition: .3s;

					&:hover {
						opacity: .7;
					}
				}
			}
		}

		.copyright, .developer {
			font-size: 14px;
			line-height: 16px;
			color: #979797;
		}

		.copyright {
			text-transform: uppercase;
		}
	}
}

#map {
	height: 100%;
	width: 100%;
	background-color: #CCC;
}

.section_map {

	.wrap_map {
		position: relative;
		height: 450px;
	}

	.map_window {
		position: absolute;
		background: #FFFFFF;
		border-radius: 10px;
		top: 50%;
		transform: translate(0, -50%);
		padding: 40px;

		.title_h4 {
			font-weight: 300;
			font-size: 32px;
			line-height: 37px;
			text-transform: uppercase;
			color: #333333;
			margin-bottom: 30px;
		}

		.list {
			margin-bottom: 32px;

			li {
				display: flex;
				align-items: center;
				margin-bottom: 10px;

				&:last-child {
					margin-bottom: 0;
				}

				span {
					font-size: 18px;
					line-height: 21px;
					color: #333333;

					.icon {
						margin-right: 16px;
						flex-shrink: 0;
					}
				}

				a {
					font-size: 18px;
					line-height: 21px;
					color: #333333;
					transition: .3s;

					.icon {
						margin-right: 16px;
						flex-shrink: 0;
					}

					&:hover {
						opacity: .7;
					}
				}
			}
		}

		.wrap_btn {
			display: flex;
			align-items: center;

			.social_box {
				margin-left: 23px;
				display: flex;
				align-items: center;

				li {
					margin-right: 5px;
					list-style: none;

					&:last-child {
						margin-right: 0;
					}

					a {
						transition: .3s;
						width: 100%;
						height: 100%;
						display: block;

						&:hover {
							opacity: .7;
						}
					}
				}
			}
		}
	}
}
