}

.forward_looking .wrap_box .list li {
  font-size: 18px;
  line-height: 21px;
  color: #000000;
  padding-left: 33px;
  list-style: none;
  background: url("../img/icon_man.svg") no-repeat left center;
  margin-right: 72px;
  margin-bottom: 17px;
}

.forward_looking .wrap_box .list li:last-child {
  margin-right: 0;
  margin-bottom: 0;
}

.section_map {
  background: #fff;
}

.overlay {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  opacity: 0;
  pointer-events: none;
  background: rgba(19, 100, 112, 0.8);
  z-index: 99;
  overflow-y: auto;
  display: flex;
  justify-content: center;
  padding: 4rem 0;
}

.popup {
  max-width: 417px;
  width: calc(100% - 30px);
  background: #fff;
  position: relative;
  overflow: hidden;
  padding: 50px 60px;
  margin: auto 0;
  border-radius: 10px;
}

@media (max-width: 768px) {
  .popup {
    padding: 50px 15px 15px 15px;
  }
}

.popup p {
  max-width: none;
  margin-bottom: 1rem;
}

.popup p:last-child {
  margin-bottom: 0;
}

.popup .title_h3 {
  font-weight: 300;
  font-size: 30px;
  line-height: 35px;
  text-transform: uppercase;
  color: #000000;
  margin-bottom: 18px;
}

.popup .sub_title {
  font-size: 18px;
  line-height: 21px;
  color: #000000;
  margin-bottom: 37px;
}

.popup .input_def {
  margin-bottom: 9px;
}

.popup .btn {
  width: 100%;
  margin-bottom: 13px;
}

.popup .info_text {
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  color: #545454;
  max-width: 224px;
  margin: 0 auto;
}

.popup.popup_2 {
  max-width: 1280px;
}

.popup.popup_2 .wrap_box {
  display: flex;
}

.popup.popup_2 .wrap_box .left_box {
  width: 451px;
  margin-right: 67px;
  flex-shrink: 0;
}

.popup.popup_2 .wrap_box .right_box .title_h3 {
  font-size: 36px;
  line-height: 42px;
  color: #333333;
  margin-bottom: 25px;
}

.popup.popup_2 .wrap_box .right_box p {
  font-weight: 300;
  font-size: 16px;
  line-height: 130%;
  text-align: justify;
  color: #000000;
  margin-bottom: 30px;
}

.popup.popup_2 .wrap_box .right_box .implementation_period, .popup.popup_2 .wrap_box .right_box .status {
  font-weight: 300;
  font-size: 16px;
  line-height: 130%;
  color: #000000;
}

.popup.popup_2 .wrap_box .right_box .implementation_period strong, .popup.popup_2 .wrap_box .right_box .status strong {
  color: #000000;
  font-weight: 400;
}

.popup.popup_2 .wrap_box .right_box .btn {
  margin-top: 30px;
  width: auto;
}

.popup.popup_3 .wrap_box .right_box .title_h3 {
  font-size: 18px;
  line-height: 130%;
  margin-bottom: 21px;
}

.input_def {
  width: 100%;
  height: 67px;
  background: #E8F8F8;
  border-radius: 5px;
  padding-left: 34px;
  padding-right: 34px;
  display: flex;
  align-items: center;
  font-size: 18px;
  color: #A4A4A4;
}

::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #A4A4A4;
}

::-moz-placeholder {
  /* Firefox 19+ */
  color: #A4A4A4;
}

:-ms-input-placeholder {
  /* IE 10+ */
  color: #A4A4A4;
}

:-moz-placeholder {
  /* Firefox 18- */
  color: #A4A4A4;
}

.body_overflow {
  overflow: hidden;
}

.close_popup {
  position: absolute;
  right: 20px;
  top: 20px;
  cursor: pointer;
}

.gallery_css .swiper-slide {
  height: 451px;
}

.gallery_css .swiper-slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  vertical-align: middle;
  border-radius: 10px;
}

.mySwiper, .mySwiper_3 {
  margin-top: 10px;
}

.mySwiper .swiper-slide, .mySwiper_3 .swiper-slide {
  height: 82px;
}

main {
  position: relative;
  overflow: hidden;
}

footer {
  padding: 36px 0;
}

footer .box {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

footer .box .footer_menu ul {
  display: flex;
  align-items: center;
}

footer .box .footer_menu li {
  list-style: none;
  margin-right: 15px;
}

footer .box .footer_menu li:last-child {
  margin-right: 0;
}

footer .box .footer_menu li a {
  font-size: 14px;
  line-height: 16px;
  color: #979797;
  transition: .3s;
}

footer .box .footer_menu li a:hover {
  opacity: .7;
}

footer .box .copyright, footer .box .developer {
  font-size: 14px;
  line-height: 16px;
  color: #979797;
}

footer .box .copyright {
  text-transform: uppercase;
}

#map {
  height: 100%;
  width: 100%;
  background-color: #CCC;
}

.section_map .wrap_map {
  position: relative;
  height: 450px;
}

.section_map .map_window {
  position: absolute;
  background: #FFFFFF;
  border-radius: 10px;
  top: 50%;
  transform: translate(0, -50%);
  padding: 40px;
}

.section_map .map_window .title_h4 {
  font-weight: 300;
  font-size: 32px;
  line-height: 37px;
  text-transform: uppercase;
  color: #333333;
  margin-bottom: 30px;
}

.section_map .map_window .list {
  margin-bottom: 32px;
}

.section_map .map_window .list li {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.section_map .map_window .list li:last-child {
  margin-bottom: 0;
}

.section_map .map_window .list li span {
  font-size: 18px;
  line-height: 21px;
  color: #333333;
}

.section_map .map_window .list li span .icon {
  margin-right: 16px;
  flex-shrink: 0;
}

.section_map .map_window .list li a {
  font-size: 18px;
  line-height: 21px;
  color: #333333;
  transition: .3s;
}

.section_map .map_window .list li a .icon {
  margin-right: 16px;
  flex-shrink: 0;
}

.section_map .map_window .list li a:hover {
  opacity: .7;
}

.section_map .map_window .wrap_btn {
  display: flex;
  align-items: center;
}

.section_map .map_window .wrap_btn .social_box {
  margin-left: 23px;
  display: flex;
  align-items: center;
}

.section_map .map_window .wrap_btn .social_box li {
  margin-right: 5px;
  list-style: none;
}

.section_map .map_window .wrap_btn .social_box li:last-child {
  margin-right: 0;
}

.section_map .map_window .wrap_btn .social_box li a {
  transition: .3s;
  width: 100%;
  height: 100%;
  display: block;
}

.section_map .map_window .wrap_btn .social_box li a:hover {
  opacity: .7;
}
